import request from "@/utils/request"

export const login = (data) => request({
  url: "/user/login",
  method: "post",
  data
})

export const sign = (data) => request({
  url: "/user/register",
  method: "post",
  data
})

export const sendMsg = (data) => request({
  url: "/user/register/sendCode",
  method: "post",
  data
})
