<template>
  <div>
    <div>
      <div
        v-if="(loginBoxSize>30)"
        class="filter showAnimate"
        @click="loginShow"
      />
      <el-card
        body-style="padding:0"
        :style="{
          marginTop:'10px',
          width:`${loginBoxSize}vw`,
          height:'98vh',
          position:'fixed',
          top:'0',
          right:'-5px',
          background:'rgba(255,255,255,0.1)',
          transition:'all 0.2s',
          borderRadius:'0px',
          borderRadius:'25px 0 0 25px',
          border:'0'
        }"
      >
        <!-- 登录界面 -->
        <div :class="{loginBoxContainer:true,showAnimate:showInnerBox,outAnimate:!showInnerBox}">
          <!-- 登录页 -->
          <el-card
            v-if="showLoginBox"
            :class="{loginBox:true,showAnimate:showLoginBox,outAnimate:!showLoginBox}"
          >
            <div class="inputBox">
              <!-- 登录标题 -->
              <div class="title">
                欢迎回来
              </div>
              <el-form
                ref="formDom"
                :model="formData"
                :rules="rules"
              >
                <el-form-item prop="name">
                  <el-input
                    v-model="formData.name"
                    style="width:280px"
                    placeholder="用户名"
                  />
                </el-form-item>
                <el-form-item prop="password">
                  <el-input
                    v-model="formData.password"
                    style="width:280px"
                    placeholder="密码"
                    type="password"
                  />
                </el-form-item>
              </el-form>
              <div class="loginBtnContainer">
                <el-button
                  class="loginBtn"
                  @click="login"
                >
                  登录
                </el-button>
              </div>
            </div>
            <div class="toSign">
              <el-button
                type="text"
                @click="toSign"
              >
                没有账号？去注册
              </el-button>
            </div>
          </el-card>
          <!-- 注册页 -->
          <el-card
            v-else
            :class="{signBox:true,showAnimate:!showLoginBox,outAnimate:showLoginBox}"
          >
            <div class="inputBox">
              <!-- 注册标题 -->
              <div class="title">
                欢迎加入我们
              </div>
              <!-- 注册表单 -->
              <el-form
                ref="signDom"
                :model="signData"
                :rules="rules"
              >
                <el-form-item prop="name">
                  <el-input
                    v-model="signData.name"
                    style="width:280px"
                    placeholder="用户名"
                  />
                </el-form-item>
                <el-form-item prop="password">
                  <el-input
                    v-model="signData.password"
                    type="password"
                    style="width:280px"
                    placeholder="密码"
                  />
                </el-form-item>
                <el-form-item prop="password2">
                  <el-input
                    v-model="signData.password2"
                    type="password"
                    style="width:280px"
                    placeholder="确认密码"
                  />
                </el-form-item>
                <el-form-item prop="email">
                  <el-input
                    v-model="signData.email"
                    style="width:280px"
                    placeholder="邮箱"
                  />
                </el-form-item>
                <el-form-item prop="code">
                  <el-input
                    v-model="signData.code"
                    style="width:140px"
                    placeholder="验证码"
                  />
                  <el-button
                    style="margin-left:25px"
                    @click="sendMsg"
                  >
                    发送验证码
                  </el-button>
                </el-form-item>
              </el-form>
              <!-- 注册按钮 -->
              <div class="signBtnContainer">
                <el-button
                  class="signBtn"
                  @click="sign"
                >
                  注册
                </el-button>
              </div>
            </div>
            <!-- 已有账号，去登录 -->
            <div class="toLogin">
              <el-button
                type="text"
                @click="toLogin"
              >
                已有账号？去登录
              </el-button>
            </div>
          </el-card>
        </div>
        <!-- 关闭按钮 -->
        <div
          v-if="closeBtnShow"
          :class="{close:true,showAnimate:closeBtnShow,outAnimate:!closeBtnShow}"
          @click="loginShow"
        >
          <i class="el-icon-close" />
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import pubsub, {
  PubSub
} from "pubsub-js"
import {
  login, sign, sendMsg
} from "@/api/login"
export default {
  name: "LoginDisk",

  data() {
    var password2 = (rule, value, callback) => {
        if (value !== this.signData.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
    return {
      closeBtnShow: false,
      loginBoxSize: 0,
      loginBoxWidth: 23,
      formData: {
        name: "",
        password: ""
      },
      rules: {
        name: [{
          required: true,
          message: "用户名不能为空",
          trigger: "blur"
        },{
          min:2,
          max:10,
          message:'用户名2-10位',
          trigger:'blur'
        }],
        password: [{
          required: true,
          message: "密码不能为空",
          trigger: "blur"
        },{
          min:6,
          max:20,
          message:'密码6-20位',
          trigger:'blur'
        }],
        password2: [{
          required: true,
          message: "确认密码不能为空",
          trigger: "blur"
        },{
          validator:password2,
          trigger:'blur'
        },{
          min:6,
          max:20,
          message:'密码6-20位',
          trigger:'blur'
        }],
        email: [{
          required: true,
          message: "邮箱不能为空",
          trigger: "blur"
        }],
        code: [{
          required: true,
          message: "验证码不能为空",
          trigger: "blur"
        }]
      },
      signData: {
        name: "",
        password: "",
        password2: "",
        email: "",
        code: ""
      },
      showLoginBox: true,
      showInnerBox: false,
      closeTimer: null,
      loginTimer: null
    }
  },
  mounted() {
    pubsub.subscribe("login", ()=>{
      this.loginShow()
    })
    if(!this.$store.state.user || !this.$store.state.user.token) {
      console.log("无token")
      this.loginBoxSize = 0
      this.loginShow()
    }
  },
  methods: {
    loginShow() {
      if(this.$refs.formDom) {
        this.$refs.formDom.resetFields()
      }
      if(this.$refs.signDom) {
        this.$refs.signDom.resetFields()
      }
      if(this.loginBoxSize === 78.2) {
        this.loginBoxSize = 0
        this.showInnerBox = !this.showInnerBox
      } else {
        this.loginBoxSize = 78.2
        this.showInnerBox = !this.showInnerBox
      }
      if(this.closeBtnShow) {
        this.closeBtnShow = false
      } else {
        if(this.closeTimer) {
          clearTimeout(this.closeTimer)
          this.closeTimer = null
        }
        this.closeTimer = setTimeout(()=>{
          this.closeBtnShow = true
        }, 500)
      }
    },
    login() {
      try {
        console.log(this.$refs.formDom)
        this.$refs.formDom.validate(async isOK=>{
          if(isOK) {
            const {
              data
            } = await login(this.formData)
            this.$store.commit("setUser", data.data)
            console.log(data)
            if(!data.data) {
              if(data.msg === "用户不存在") {
                this.$message.warning("用户不存在")
                this.showLoginBox = false
                this.$refs.formDom.resetFields()
              }
            } else {
              this.$message.success("登陆成功")
              PubSub.publish("updateUserInfo")
              this.loginShow()
            }
          }
        })
      } catch (error) {
        this.$refs.formDom.resetFields()
        this.$message.warning(error)
      }
    },
    sign() {
      try {
        this.$refs.signDom.validate(async isOK=>{
          if(isOK) {
            const {
              data
            } = await sign(this.signData)
            console.log(data)
            this.showLoginBox = true
            if(data.msg) {
              this.$message.warning(data.msg)
            }
          }
        })
      } catch (error) {
        this.loginShow()
        this.$refs.signDom.resetFields()
        this.$message.warning(error)
      }
    },
    sendMsg() {
      let validateFieldList = []
      this.$refs.signDom.validateField(["name", "password", "password2", "email"], async(valid) => {
        if (!valid) {
          validateFieldList.push(valid)
          if (validateFieldList.length == 4 && validateFieldList.every((item) => item === "")) {
            const {data}= await sendMsg(this.signData)
            console.log(data)
            if(data.msg){
              this.$message.warning(data.msg)
            }
          }
          return
        }
      })
    },
    toSign() {
      this.$refs.formDom.resetFields()
      this.showLoginBox = false
    },
    toLogin() {
      this.$refs.signDom.resetFields()
      this.showLoginBox = true
    }
  }
}
</script>

<style lang="less" scoped>
.filter{
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  filter: blur(10px);
}
.showAnimate{
    animation: fadeIn;
    animation-duration: 1s;
}
.outAnimate{
    animation: fadeOut;
    animation-duration: 0.8s;
}
.close{
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 40px;
}

.loginBoxContainer{
    height: 95vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .selectBtn{
        width:15vw;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .btnText{
            font-size: 2vw;
            font-weight: 800;
            color: black;
            font-family: "仓耳渔阳体 W03";
        }
        .spite{
            width:4px;
            height: 50%;
            background-color: #000000;
        }
    }
    .loginBox{
        width:450px;
        height: 450px;
        position: relative;
        border-radius: 20px;
        .toSign{
            position: absolute;
            bottom: 9vh;
            left: 4vw;
        }
        .inputBox{
            width: 425px;
            display: flex;
            justify-items: center;
            align-items: center;
            flex-direction: column;
            .title{
                font-size: 1.8vw;
                font-weight:500;
                font-family: "仓耳渔阳体 W02";
                margin: 20px;
            }

            .loginBtnContainer{
                width:100%;
                display: flex;
                justify-content: center;
                .loginBtn{
                    width:280px;
                    background-color: #000000;
                    color: #fff;
                }
                .loginBtn:hover{
                    background:rgba(0, 0, 0, 0.7);
                }

            }
        }
    }
    .signBox{
        width:450px;
        height: 500px;
        position: relative;
        border-radius: 20px;
        padding-bottom: 100px;
        .toLogin{
            position: absolute;
            bottom: 9vh;
            left: 4vw;
        }
        .inputBox{
            width: 425px;
            display: flex;
            justify-items: center;
            align-items: center;
            flex-direction: column;
            animation: fadeIn;
            animation-duration: 2s;
            .title{
                font-size: 1.8vw;
                font-weight:normal;
                font-family: "仓耳渔阳体 W02";
                margin: 20px;
            }
            .signBtnContainer{
                width:100%;
                display: flex;
                justify-content: center;
                .signBtn{
                    width:280px;
                    background-color: #000000;
                    color: #fff;
                }
                .signBtn:hover{
                    background:rgba(0, 0, 0, 0.7);
                }
            }
        }
    }
}
</style>
